* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.form-js {
  justify-content: center;
  display: flex;
}

.btn {
  margin-left: 20px;
}

.unvisible {
  display: none;
}

.cat-info {
  gap: 15px;
  margin-top: 25px;
  margin-bottom: 25px;
  display: flex;
}

.cat-picture {
  max-width: 100%;
  height: auto;
  display: block;
}

.cat-desc, .cat-temp, .cat-origin {
  font-size: 16px;
  line-height: 120%;
}

.loader {
  perspective: 1000px;
  width: 48px;
  height: 48px;
  color: #0bde6a;
  border-radius: 50%;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

